import React from 'react';
import { Button, Card, ListGroup } from 'react-bootstrap';
import { text } from './Text.js';
import Header from './Header.js'
import { useState } from 'react';

export default function Main() {

  const [index,setIndex] = useState(Math.floor( Math.random() * text.length ));
  const [isAns,setIsAns] = useState(false);

  let problems = [];
  let answers = [];
  text.forEach(function(item) {
    let memo = item;
    let char = memo.split('');
    char.sort();
    let s = '';
    char.forEach(function(item2){
      s+=item2;
    })
    answers.push(item);
    problems.push(s);
  })
  
  const setNext = () => {
    setIsAns(false);
    setIndex(Math.floor( Math.random() * text.length ));
  }

  return (
    <div>
      <Header></Header>
      
      <Card style={{ width: '36rem' }}>
        <ListGroup variant="flush">
          <ListGroup.Item>問題：{problems[index]}</ListGroup.Item>
          {isAns ? <ListGroup.Item>答え：{answers[index]}</ListGroup.Item>:<ListGroup.Item>答え：</ListGroup.Item>}
        </ListGroup>
      </Card>
    
      <Button variant="primary" onClick={()=>setIsAns(IsAns => !IsAns)}>Show Answer</Button>{' '}
      <Button variant="primary" onClick={setNext}>Next</Button>
    </div>
  );
}